
.home-page {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	overflow: hidden;
	main, .home-site {
		width: 100%;
		height: 100%; }
	.typings {
		width: 100%;
		bottom: 0;
		left: 0;
		position: absolute;
		z-index: 700;
		padding: 2rem;
		color: $color-white;
		.typing {
			white-space: pre;
			line-height: 1.25em;
			color: #a8acb9;
			font-size: .9rem;
			-webkit-font-smoothing: none;
			@include text-shadow(0px 1px 2px #000);
			&:after {
				visibility: visible;
				content: '';
				background-color: #36b455;
				display: inline-block;
				position: relative;
				width: 0.5em;
				height: .7rem;
				top: 1px;
				margin-left: .25rem;
				-webkit-animation: blink 0.7s infinite;
				-moz-animation: blink 0.7s infinite;
				animation: blink 0.7s infinite; }
			@include media-breakpoint-up(md) {
				font-size: 1.3rem;
				&:after {
					height: 1.2rem;
					top: 2px; } } }

		.typed-cursor {
			display: none;
			color: $color-white;
			opacity: 1;
			-webkit-animation: blink 0.7s infinite;
			-moz-animation: blink 0.7s infinite;
			animation: blink 0.7s infinite; } }
	#particles-js {
		width: 100%;
		height: 100%;
		> div {
			@include opacity(.3); } }
	.spinner-circle {
		position: absolute;
		z-index: 800;
		top: calc(50% - 25px);
		left: calc(50% - 25px);
		width: 50px;
		height: 50px;
		border-width: 10px;
		border-color: $color-con4 transparent $color-con4 transparent;
		@include opacity(.7);
		@include animation(spinner-rotate-outer 2s ease-in-out infinite);
		position: absolute;
		background-color: transparent;
		@include border-radius(50%);
		border-style: solid;
		&:before, &:after {
			position: absolute;
			background-color: transparent;
			@include border-radius(50%);
			border-style: solid; }
		&:before {
			content: '';
			position: absolute;
			width: 24px;
			height: 24px;
			border-width: 6px;
			border-color: $color-con2 transparent $color-con2 transparent;
			top: 3px;
			left: 3px;
			opacity: .9;
			@include animation(spinner-rotate-inner 3s linear infinite); }
		&:after {
			content: '';
			width: 0;
			height: 0;
			border-width: 30px;
			top: -15px;
			left: -15px;
			opacity: .6;
			@include animation(spinner-rotate-single-2 7s 0s ease-in-out infinite);
			border-color: transparent transparent transparent $color-white;
			@include box-shadow(2px, 0, 2px, $color-con1); } }
	.spinner-circle2 {
		z-index: 800;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		div {
			position: absolute;
			left: 50%;
			top: 50%;
			display: block;
			background: transparent;
			border: 10px solid $color-con3;
			border-left-color: transparent;
			border-right-color: transparent;
			@include border-radius(50%);
			transition: all 0.5s ease;
			&:last-child {
				position: absolute;
				left: 50%;
				top: 50%;
				z-index: 20;
				width: 200px;
				height: 200px;
				margin-left: -102px;
				margin-top: -102px;
				padding: 70px 0px 30px;
				// background-color: rgba(red($color-white),green($color-white),blue($color-white),.15)
				border: 10px solid $color-con1;
				@include border-radius(50%);
				text-shadow: 2px 2px 0px rgba(0, 0, 0, 1);
				// +box-shadow(0px, 0px, 30px, $color-main)
				@include animation(pulseShadow 5s infinite linear);
				// &:after
				// 	content: ''
				// 	border: 1px dashed $color-con
				// 	+border-radius(50%)
				// 	width: 200px
				// 	height: 200px
				// 	display: block
				// 	position: absolute
				// 	top: -3px
				// 	left: -3px
				// 	background-color: transparent
 }				// 	box-shadow: inset 0px 0px 30px rgba(0, 0, 0, 1)
			&:first-child {
				margin-left: -117px;
				margin-top: -117px;
				z-index: 2;
				width: 230px;
				height: 230px;
				border-width: 12px;
				border-bottom-color: transparent;
				@include animation(spinBG 5s infinite linear); }
			&:nth-child(2) {
				margin-left: -137px;
				margin-top: -137px;
				z-index: 1;
				width: 270px;
				height: 270px;
				border-width: 6px;
				border-style: solid;
				border-color: $color-con2;
				border-bottom-color: transparent;
				border-top-color: transparent;
				// +box-shadow(0px, 0px, 20px, $color-con)
				@include animation(spinBG2 2s infinite linear); }
			&:nth-child(3) {
				margin-left: -150px;
				margin-top: -150px;
				z-index: 1;
				width: 296px;
				height: 296px;
				border-width: 2px;
				border-color: $color-con;
				border-bottom-color: transparent;
				border-left-color: transparent;
				@include box-shadowset(inset 0px 0px 15px rgba(red($color-con),green($color-con),blue($color-con),.4));
				@include animation(spinBG 12s infinite linear); }
			&:nth-child(4) {
				margin-left: -127px;
				margin-top: -127px;
				z-index: 1;
				width: 250px;
				height: 250px;
				border-width: 5px;
				border-style: solid;
				border-top-color: $color-main;
				border-bottom-color: $color-con4;
				border-right-color: transparent;
				border-left-color: transparent;
				@include box-shadowset(inset 0px 0px 25px $color-con4);
				@include animation(spinBG3 8s infinite linear); } } } }
